.container {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.locationContainer {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

.title {
    font-size: 40px;
}

.buttonContainer {
  display: flex;
  justify-content: center;
}

.buttonShowMore {
  width: 150px;
  padding: 10px 10px;
  background-color: #000;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 8px;
  cursor: pointer;

  &:hover {
    border: 1px solid yellow;
    color: yellow;
  }
}